import React, {Component} from 'react';

export default class Blocks extends Component{
    constructor(props){
        super(props);

        this.state = {
            mouseY: 0,
            blocksHeight: 100,
        };

    }


    resizeHandle(that,e){
        const element = document.querySelector('.blocksContainer');
        console.log(element,e.clientY);
        this.setState({
            mouseY: e.clientY,
        });
    }

    resizeHandleMove(that,e){
        const element = document.querySelector('.blocksContainer');
        this.setState({
            mouseY: e.clientY,
        });

        let newHeight = this.state.blocksHeight + (e.clientY - this.state.mouseY);

        console.log('NEW HEIGHT', newHeight, this.state.mouseY );

        this.setState({
            blocksHeight: newHeight
        });

        newHeight += 'px';

        element.style.height = newHeight;
    }

    render(){

        return(
            <div className="blocks">
                <div className="blocksContainer">
                    <div
                        className="blocksTopLine"
                        onMouseDown={this.resizeHandle.bind(this,event)}
                        onMouseMove={this.resizeHandleMove.bind(this,event)}
                    />

                </div>
            </div>
        );
    }
}